import { Component,OnInit ,ViewChild} from '@angular/core';
import { ChatService } from './chat.service';
import * as io from 'socket.io-client';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class  AppComponent  {
  title = 'app';
  peer;
  private socket;
  anotherid;
  public startCall=true;
  public Oncalled=false;
  public stRecord=false;
  public audio = new Audio();
  public currUser:any;
 public isLogined:boolean;
  public userL:any;
  public password:any;
  public user:any;
  public username:any;
  public mypeerid:any;
  public startTimeCall:boolean=false;
  @ViewChild('myvideo') myVideo: any;
  @ViewChild('revideo') reVideo: any;

  constructor(public chatservice:ChatService){
    this.audio.src = "../assets/music/nhacchuong.mp3";
    this.audio.load();
    this.socket = io(chatservice.getUrl());
    this.peer = new Peer({ key: 'n90yu2ztuf03sor' });
  }
  
  ngOnInit(){
    
    setTimeout(() => {
      this.mypeerid = this.peer.id;
      
    }, 2000);
    this.peer.on('connection', function (conn) {
      alert(conn)
      conn.on('data', function (data) {
        console.log(data);
      });
    });
    //  this.chatservice.connect().then((userList:any)=>{
    //   this.userL=userList;
    //   this.currUser=userList[0]
    //  })
    this.peer.on('call',call=>{
      console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!")
      this.audio.play();
      this.Oncalled=true
      this.waiting().then((res:any)=>
      {
        this.audio.pause();
        if(res===1)
        {
        
      let videor = this.myVideo.nativeElement;
      openStream().then(stream=>{
        this.Oncalled=false
        call.answer(stream)
        videor.src = URL.createObjectURL(stream);
        this.startCall=false
        this.startTimeCall=true;
        videor.play();
        call.on("stream",remoteStream=>{
          let video = this.reVideo.nativeElement;
          video.src = URL.createObjectURL(remoteStream);
        video.play();
        })
      })
    }
    })
    })
    
  }
  startRecord(){
    this.stRecord=true;
  }
  login(){
    setTimeout(() => {
      
    
console.log(this.username+"   "+this.password)
    this.chatservice.login({username:this.username,password:this.password,peerID:this.mypeerid}).then((res:any)=>{
      if(res.err===0)
     
        
      this.user=res.data.data;
     console.log("this.user"+this.userL)
      console.log("this.mypeerid"+this.user.peerID)
      this.isLogined=true;
     
      this.socket.on('userList', (data) => {
        console.log(data)

        console.log(data)
        this.userL=data;
        if(this.userL.length>0)
        {
        for(var i=0;i<this.userL.length;i++)
          if(this.user.username===this.userL[i].username)
          this.userL.splice(i,1)
        this.currUser=data[0]
        console.log(this.currUser)
      }
        
      })
     
    })
  }, 2000);
  }
  disconnect(){
     
      this.startTimeCall=false;
      this.stRecord=false;
      this.Oncalled=false;
      this.startCall=true
    // })
  }
  connect() {
    console.log(this.currUser.peerID)
   this.anotherid = this.currUser.peerID;
   console.log(this.anotherid)
    this.stRecord=true;
    this.startCall=false;
    // var conn = this.peer.connect(this.anotherid);
    // conn.on('open', function () {
    //   conn.send('Message from that id');
    // });
   
    let video = this.reVideo.nativeElement;
    openStream().then(stream=>{
      console.log(JSON.stringify(stream))
      video.src = URL.createObjectURL(stream);
        video.play();
        this.Oncalled=true;
        const call=this.peer.call(this.anotherid,stream);
        call.on("stream",remoteStream=>{
          this.startTimeCall=true;
          let videor = this.myVideo.nativeElement;
          console.log(JSON.stringify(remoteStream))
          videor.src = URL.createObjectURL(remoteStream);
          videor.play();
        })
  })
 
  }
  waiting(){
    return new Promise((resolve,reject)=>{
      if(this.stRecord===true)
      {
        
        resolve(1)
      }
      else{
        setTimeout(() => {
          resolve(this.waiting())
        }, 2000);
       
      }
    })
  }
  ckeckCkickAs(){
      
  }

}

function openStream(){
  const config={
      audio:true,
      video:true
  }
  return navigator.mediaDevices.getUserMedia(config);
}



